.main {
    height: max-content;
    width: 100%;
    min-height: 900px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 30px 0;
    background: url("../../assets/bcg.png") no-repeat center 0;
    background-size: cover;
    margin-bottom: 100px;
}

